<template>
  <div>
    <div id="app">{{ info }}</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      info: null
    };
  },
  mounted() {
    this.$axios
      .get("http://192.168.11.140/api/Values/prose")
      .then(response => (this.info = response.data))
      .catch(function(error) {
        // 请求失败处理
        console.log(error);
      });
  }
};
</script>

<style>
</style>


